.home{
  background-image: url("../../assets/bg.jpg");
  min-height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 50px;
  .container{
    width: 1200px;
    max-width: 100%;
    padding: 0 10px;
    margin: 0 auto;
    position: relative;
    .countdownWrapper{
      color: black;
      text-align: center;
      font-size: 16px;
      .Countdown{
        text-align: center;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        .Countdown-col{
          display: flex;
          flex-direction: row;
          color: black;
          justify-content: center;
          align-items: center;
          font-size: 25px;
        }
        .Countdown-col-element{
          color: black;
          font-size: 30px;
        }
      }

    }

  }
  .header{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 50px 0 0 0;
    .walletWrapper{
      user-select: none;
      width: 250px;
      color: #8247E5;
      font-size: 16px;
      padding: 5px 30px;
      border: 1px solid #8247E5;
      border-radius: 20px;
      text-align: center;
      font-weight: bold;
      &:hover{
        cursor: pointer;
        color: white;
        background-color: #8247E5;
      }
    }

    .logo{
      width: 250px;
      margin-bottom: 30px;
    }
    .socialMedias{
      width: 250px;
      text-align: right;
      a{
        margin: 0 10px;
      }
      img{
        width: 27px;
      }
    }
  }
  .networkButtons{

    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    gap: 20px;
    div{
      width: 150px;
      border: 1px solid #8247E5;
      border-radius: 20px;
      padding: 5px 0;
      font-size: 16px;
      text-transform: capitalize;
      font-weight: bold;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #8247E5;
      cursor: pointer;
      img{
        width: 25px;
        margin-right: 10px;
      }
      &:hover{
        box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.25);
      }
    }
    .bsc{
      border-color:#E58F0E;
      color: #E58F0E;
    }

  }
  .slogan{
    font-size: 16px;
    line-height: 16px;
    color: black;
    text-align: center;
  }
  .buttonGroup{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-top: 20px;
  }

  .whitepaper{
    //display: block;
    width: 180px;
    text-align: center;
    font-size: 18px;
    font-weight: 600;
    text-transform: uppercase;
    border-radius: 10px;
    border: 2px solid #EFEFEF;
    background-color: #8247E5;
    padding: 5px 0;
    box-shadow: 0 3px black;
    color: white;
    a{
      color: white;
    }

    &:hover{
      cursor: pointer;
    }
    &:active {
      background-color: black;
      box-shadow: 0 3px #EFEFEF;
      transform: translateY(2px);
    }
  }
  .mainContent{
    width: 800px;
    max-width: 100%;
    margin: 50px auto 0 auto;
    display: flex;
    justify-content: center;
    align-items: stretch;
    gap: 20px;
    .box{
      width: calc(50% - 10px);
    }
    .leftBox{
      padding: 20px;
      border-radius: 20px;
      background: #FFFFFF;
      box-shadow: 0 0 16px rgba(0, 0, 0, 0.25);
    }
    .rightBox{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .dataRow{
      display: flex;
      justify-content: space-between;
      align-content: center;
      margin: 5px 0;
      color: black;
      font-size: 18px;
      line-height: 35px;
      .name{

      }
      .value{
        font-weight: 600;
        font-size: 22px;
        color: black;
      }

    }

    .ant-input-affix-wrapper {
      height: 55px;
      margin: 0;
      position: relative;
      border: 2px solid #000000;
      box-shadow: none;
      backdrop-filter: blur(20px);
      box-sizing: border-box;
      border-radius: 40px;
      background: rgba(130, 71, 229, 0.1);
      .ant-input-suffix,
      .ant-input-prefix {
        color: #8247E5;
        font-size: 16px;
        font-weight: 600;
        cursor: pointer;
        user-select: none;
        span {
          color: #8247E5;
        }
      }
      .suffix {
        padding-right: 10px;
      }

      .ant-input {
        border: none;
        color: #8247E5;
        background: transparent;
        font-size: 16px;
        font-weight: 600;
        line-height: 55px;
        text-align: center;
        padding-left: 50px;
      }

      .ant-input:hover {
        border: none !important;
      }
      .ant-input:focus {
        border: none !important;
        outline: 0;
        box-shadow: none !important;
      }
    }

    .ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover{
      border-color: #000000 !important;
      border-right-width: 2px;
    }

    .ant-input-affix-wrapper-focused {
      outline: 0;
      border: 2px solid #000000 !important;
      box-shadow: none !important;
    }

    .buyButton{
      border: 2px solid #000000;
      border-radius: 30px;
      color: #000000;
      font-size: 16px;
      font-weight: 600;
      line-height: 42px;
      padding: 5px 0;
      text-align: center;
      margin-top: 10px;

      &:hover{
        cursor: pointer;
        border: 2px solid black;
        color: black;
      }
    }

    .actionWrapper{
      margin-top: 30px;
      border-top: 1px dashed #8247E5;
      padding-top: 10px;

      .actionButtons{
        display: flex;
        justify-content: space-between;
        align-items: center;
        div{
          width: calc(50% - 10px);
          text-align: center;
          border: 2px solid black;
          color: black;
          font-weight: bold;
          font-size: 16px;
          padding: 8px 0;
          border-radius: 30px;
          &:hover{
            cursor: pointer;
            border: 2px solid black;
            color: #8247E5;
          }
        }
      }
    }

    .contractInfo{
      background: #8247E5;
      border-radius: 20px;
      box-shadow: 0 0 16px rgba(0, 0, 0, 0.25);
      padding: 20px;
      display: flex;
      align-content: center;
      justify-content: space-between;
      img{
        width: 25%;
      }

      .data{
        width: 65%;

        h1{
          color:white;
          font-size: 25px;
          font-weight: bold;
          margin-bottom: 10px;
        }
        .dataRow{
          font-size: 17px;
          color: white;

          .value{
            color: white;
          }
        }
      }

    }

    .referral{
      background: #EFEFEF;
      box-shadow: 0px 0px 16px rgba(0, 0, 0, 0.25);
      border-radius: 20px;
      padding: 20px;
      margin-top: 20px;
      h1{
        color:black;
        font-size: 25px;
        font-weight: bold;
        margin-bottom: 10px;
      }
      p{
        color:black;
      }
      .refWrapper{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .referralLink{
          border: 2px solid black;
          box-sizing: border-box;
          border-radius: 40px;
          padding: 5px 20px;
          color: black;
          flex-grow: 2;
          text-align: center;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          max-width: 70%;
        }
        .copyButton{
          border: 2px solid black;
          box-sizing: border-box;
          border-radius: 40px;
          padding: 5px 20px;
          color: black;

          &:hover{
            cursor: pointer;
            border: 2px solid black;
            color: black;
          }
        }
      }
    }
  }

  @media(max-width: 765px){
    .header{
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      flex-direction: column;
      padding: 20px 0 0 0;
      margin-bottom: 30px;
      .walletWrapper{
        margin-left: auto;
        width: 150px;
        font-size: 15px;
        padding: 5px 0;
      }

      .logo{
        width: 200px;
        margin: 0 auto;
      }
      .socialMedias{
        width: 100%;
        text-align: center;
        margin: 30px auto 0;
        a{
          margin: 0 10px;
        }
        img{
          width: 25px;
        }
      }
    }
    .slogan{
      line-height: 20px;
    }
    .buttonGroup{
      display: flex;
      flex-direction: column;
    }
    .mainContent{
      margin: 30px auto 0 auto;
      align-items: center;
      flex-direction: column;
      gap: 10px;
      .box{
        width: calc(100% - 20px);
      }

      .referral{
        margin-top: 10px;
      }
    }
  }
}

.blink_me {
  animation: blinker 2s linear 1;
}

@keyframes blinker {
  50% { color: #8247E5; transform: scale(1.1) }
}